<template>
  <q-page class="bg-white">
    <q-card flat class="container bg-trasnparent">
      <q-card flat>
        <div class="row q-col-gutter-md items-center q-pb-lg">
          <div
            class="col-12 col-sm-auto"
            :style="`min-width: ${splitterModel + 35}px;`"
          >
            <div class="flex flex-start">
              <q-avatar
                square
                :color="brandAvatar.id ? 'transparent' : 'accent'"
                size="136px"
                class="rounded"
                text-color="secondary font-medium"
              >
                <img
                  v-if="brandAvatar.id"
                  :src="brandAvatar.download_url"
                  :alt="avatarNameAbbr(brandInfo.brand_name)"
                  class="border-add q-pa-sm row items-center justify-center"
                />
                <span v-else-if="brandInfo.brand_name">
                  {{ avatarNameAbbr(brandInfo.brand_name) }}
                </span>
                <q-icon v-else name="icon-star-asterisk" size="60px" />
              </q-avatar>
            </div>
          </div>
          <div class="col-12 col-sm-auto">
            <q-breadcrumbs
              active-color="primary-500"
              separator-color="primary-500"
              gutter="xs"
            >
              <template v-slot:separator>
                <q-icon size="xs" name="icon-chevron-right" class="q-py-xs" />
              </template>
              <q-breadcrumbs-el
                icon="icon-home-line"
                class="q-py-xs q-pr-sm cursor-pointer hover-text-secondary"
                to="/"
              />
              <q-breadcrumbs-el
                label="Brands"
                class="q-py-xs q-px-sm rounded cursor-pointer hover-text-secondary text-primary-500 font-medium"
                to="/brands"
              />
              <q-breadcrumbs-el
                label="Brand Overview"
                class="q-py-xs q-px-sm rounded cursor-pointer hover-text-secondary text-primary-500 font-medium"
                :to="`/brands/${brandId}`"
              />
              <q-breadcrumbs-el
                label="Project Overview"
                class="text-secondary bg-accent q-py-xs q-px-sm rounded font-medium"
              />
            </q-breadcrumbs>
            <div class="q-pt-md q-pb-sm" id="overview-title">
              <div class="row items-center q-gutter-sm">
                <q-spinner
                  v-if="!projectInfo.title"
                  color="secondary"
                  size="sm"
                  class="q-mr-sm"
                />
                <span class="text-h4 font-demi-bold text-primary">
                  {{ projectInfo.title || 'Loading project...' }}
                </span>
                <q-badge
                  v-if="projectInfo.title"
                  class="badge badge-blue cursor-pointer"
                  @click="$router.push(`/brands/${brandId}`)"
                >
                  {{ brandInfo.brand_name }}
                </q-badge>
              </div>
            </div>
          </div>
        </div>
      </q-card>

      <!-- Body -->

      <q-card flat>
        <q-splitter
          v-model="splitterModel"
          unit="px"
          id="mainSplitter"
          ref="mainSplitter"
          :separator-class="`no-pointer-events bg-transparent q-mx-md${
            $q.screen.lt.sm ? ' hidden' : ''
          }`"
          :before-class="`sticky sticky-menu-top${
            $q.screen.lt.sm ? ' hidden  ' : ''
          }`"
          after-class="overflow-view"
        >
          <template v-slot:before>
            <q-card v-if="projectInfo.title" flat class="q-gutter-sm">
              <q-card
                flat
                v-ripple="{ color: 'secondary' }"
                v-for="(tab, i) in allSections"
                :key="tab.key"
                class="cursor-pointer rounded hover-text-secondary hover-bg-accent text-body1 font-medium"
                :class="
                  (tab.key === tabSectionId && tabSectionIdScrolled === '') ||
                  tabSectionIdScrolled === tab.key ||
                  (tabSectionIdScrolled === '' &&
                    tabSectionId === '' &&
                    i === 0)
                    ? 'bg-accent text-secondary'
                    : 'text-primary-500'
                "
                @click="clickScrollEvent(tab.key, false)"
                :section-id="tab.key"
              >
                <q-btn
                  flat
                  no-caps
                  :ripple="false"
                  class="rounded hover-text-secondary hover-bg-accent font-medium button-md"
                >
                  <div class="full-width text-left text-body1">
                    {{ tab.label }}
                  </div>
                </q-btn>
              </q-card>
            </q-card>
            <template v-else>
              <div v-for="j in [1, 2, 3, 4]" :key="j">
                <q-skeleton type="QToolbar" class="q-my-md" height="2rem" />
              </div>
            </template>
          </template>

          <template v-slot:after>
            <q-card v-if="projectInfo.title" flat class="text-body1 q-pb-xl">
              <q-card-section
                class="q-px-none q-pt-none q-pb-md font-medium text-h5"
              >
                <div class="row justify-end items-center">
                  Project Overview
                  <q-space />

                  <div v-if="!editing" class="q-gutter-sm">
                    <q-btn
                      v-if="documentReady"
                      flat
                      round
                      size="sm"
                      icon="icon-printer"
                      class="text-primary-500 hover-text-secondary"
                      :disable="!documentReady || convertingPDF"
                      @click="
                        exportToPDF(
                          `PROJECT-${projectInfo?.title}-${brandInfo.brand_name}`,
                          ['overview-title', ...overviewIds]
                        )
                      "
                    >
                      <tooltip-pro
                        :anchor="`${
                          $q.screen.lt.md ? 'center left' : 'bottom middle'
                        }`"
                        :self="`${
                          $q.screen.lt.md ? 'center right' : 'top middle'
                        }`"
                      >
                        Export to PDF
                      </tooltip-pro>
                    </q-btn>
                    <q-btn
                      flat
                      round
                      size="sm"
                      icon="icon-edit-05"
                      class="text-primary-500 hover-text-secondary"
                      :disable="!documentReady || convertingPDF"
                      @click="editing = true"
                    >
                      <tooltip-pro>Edit</tooltip-pro>
                    </q-btn>
                  </div>
                  <div v-else class="q-gutter-sm">
                    <q-btn
                      no-caps
                      outline
                      size="sm"
                      class="rounded not-gm hover-negative"
                      @click.stop="editing = false"
                    >
                      Cancel
                    </q-btn>
                    <q-btn
                      no-caps
                      unelevated
                      size="sm"
                      color="primary"
                      class="rounded not-gm hover-bg-secondary"
                      @click.stop="editing = false"
                    >
                      <q-icon name="icon-save-01" size="xs" />
                      <span class="q-pl-xs">Save Changes</span>
                    </q-btn>
                  </div>
                </div>
              </q-card-section>
              <template v-for="(data, id, i) in visibleAnswers" :key="data">
                <q-card-section
                  class="q-px-none q-pt-none q-pb-lg"
                  :q-id="i"
                  :q-info="JSON.stringify(data)"
                  :id="overviewIds[i]"
                >
                  <div
                    class="q-primary-title full-width text-left"
                    :class="{
                      ['q-pb-md']: editing,
                    }"
                  >
                    <div class="font-medium text-h5 q-py-sm">
                      <span
                        class="cursor-pointer"
                        @click="
                          () =>
                            noTitleClick
                              ? void 0
                              : clickScrollEvent(overviewIds[i])
                        "
                      >
                        {{ data.question?.title }}
                      </span>
                    </div>
                    <div v-if="editing" class="text-body2 text-primary-500">
                      {{ data.question?.question }}
                    </div>
                  </div>
                  <!-- Primary -->
                  <DynamicAnswers
                    v-if="!editing"
                    :qid="data.question?.id"
                    :qtype="data.question?.type"
                    :aqtype="data.question?.alternative_type"
                    :question="{
                      title: data.question?.title,
                      question: data.question?.question,
                      options: data.question.options,
                    }"
                    :answer="data.value"
                    class="q-primary-answer full-width q-pb-md"
                  />
                  <div v-else class="q-primary-question full-width q-pb-md">
                    <DynamicQuestions
                      :autofocus="false"
                      :question="data.question"
                      :initAnswer="answer[data.question?.id]"
                      :platforms="requestInfo.platforms"
                      @answered="(ans) => (answer[data.question?.id] = ans)"
                      :textareaRow="15"
                      :ref="
                        data.question?.type.startsWith('upload_')
                          ? `upload${data.question?.id}`
                          : undefined
                      "
                      :fileRemovedFile="
                        uploads[`upload${data.question?.id}`]?.removedFile
                      "
                      :fileFactory="factoryFnAutoUpload"
                      :fileInitSlots="uploads[`upload${data.question?.id}`]"
                      fileThumbnail="auto"
                      @checked="
                        (val) => (answer[data.question?.id].value.info = val)
                      "
                      :checked="answer[data.question?.id]?.value?.info"
                      @fileAdded="
                        (files) => {
                          if (
                            !uploads[`upload${data.question?.id}`]?.files
                              ?.length ||
                            data.question?.type === 'website_content'
                          ) {
                            addUploadToModel(
                              'uploads',
                              `upload${data.question?.id}`,
                              files,
                              `upload${data.question?.id}`
                            );
                          } else if (
                            !uploads[`upload${data.question?.id}`]?.files
                              ?.length ||
                            data.question?.type === 'upload_multiple'
                          ) {
                            addUploadToModel(
                              'uploads',
                              `upload${data.question?.id}`,
                              files,
                              `upload${data.question?.id}`
                            );
                          } else {
                            uploads[`upload${data.question?.id}`].files = files;
                          }
                        }
                      "
                      @fileUploaded="
                        ({ files, xhr }) => {
                          const response = JSON.parse(xhr.response || '{}');
                          if (response.success) {
                            const data = response.data;
                            if (data.question?.type === 'website_content') {
                              answer[data.question?.id].value.uploads = answer[
                                data.question?.id
                              ].value.uploads.concat(data.map((e) => e.id));
                            } else if (
                              data.question?.type === 'upload_multiple'
                            ) {
                              answer[data.question?.id].value = answer[
                                data.question?.id
                              ].value.concat(data.map((e) => e.id));
                            } else {
                              answer[data.question?.id].value = data[0]?.id;
                            }
                          }
                        }
                      "
                      @fileRemoved="
                        (files) => {
                          if (data.question?.type === 'website_content') {
                            files
                              .map((e) => e.id)
                              .forEach((e) => {
                                const ind = answer[
                                  data.question?.id
                                ]?.value.uploads?.findIndex?.((a) => a === e);
                                if (ind !== -1) {
                                  answer[
                                    data.question?.id
                                  ].value.uploads.splice(ind, 1);
                                }
                              });
                          } else if (
                            data.question?.type === 'upload_multiple'
                          ) {
                            files
                              .map((e) => e.id)
                              .forEach((e) => {
                                const ind = answer[
                                  data.question?.id
                                ]?.value.uploads?.findIndex?.((a) => a === e);
                                if (ind !== -1) {
                                  answer[data.question?.id].value.splice(
                                    ind,
                                    1
                                  );
                                }
                              });
                          } else {
                            answer[data.question?.id].value = '';
                          }
                        }
                      "
                    ></DynamicQuestions>
                  </div>

                  <!-- Alternative -->
                  <template
                    v-if="
                      data.question?.alternative_if &&
                      (answer[data.question?.id]?.value ===
                        data.question?.alternative_if ||
                        answer[data.question?.id]?.value?.value ===
                          data.question?.alternative_if)
                    "
                  >
                    <div
                      v-if="data.question?.alternative_question && editing"
                      class="q-alternative-title full-width text-left q-pb-md"
                    >
                      <div class="text-body2 text-primary-500">
                        {{ data.question?.alternative_question }}
                      </div>
                    </div>
                    <DynamicAnswers
                      v-if="!editing"
                      :qid="data.question?.id"
                      :qtype="data.question?.alternative_type"
                      :question="{
                        title: data.question?.title,
                        question: data.question?.alternative_question,
                        options: data.question.alternative_options,
                      }"
                      :answer="data.alternative_answer"
                      class="q-alternative-answer full-width q-pb-md"
                    />
                    <div
                      v-else
                      class="q-alternative-question full-width q-pb-md"
                    >
                      <DynamicQuestions
                        :autofocus="false"
                        :question="{
                          id: data.question?.id,
                          type: data.question?.alternative_type,
                          required: data.question?.required,
                          options: data.question?.alternative_options,
                          placeholder: data.question?.alternative_placeholder,
                        }"
                        :initAnswer="{
                          value: answer[data.question?.id].alternative_answer,
                        }"
                        :platforms="requestInfo.platforms"
                        @answered="
                          (ans) =>
                            (answer[data.question?.id].alternative_answer =
                              ans?.value)
                        "
                        :textareaRow="15"
                        :ref="
                          data.question?.alternative_type.startsWith('upload_')
                            ? `upload${data.question?.id}`
                            : undefined
                        "
                        :fileRemovedFile="
                          uploads[`upload${data.question?.id}`]?.removedFile
                        "
                        :fileFactory="factoryFnAutoUpload"
                        :fileInitSlots="uploads[`upload${data.question?.id}`]"
                        fileThumbnail="auto"
                        @checked="
                          (val) => (answer[data.question?.id].value.info = val)
                        "
                        :checked="answer[data.question?.id]?.value?.info"
                        @fileAdded="
                          (files) => {
                            if (
                              !uploads[`upload${data.question?.id}`]?.files
                                ?.length ||
                              data.question?.alternative_type ===
                                'website_content'
                            ) {
                              addUploadToModel(
                                'uploads',
                                `upload${data.question?.id}`,
                                files,
                                `upload${data.question?.id}`
                              );
                            } else if (
                              !uploads[`upload${data.question?.id}`]?.files
                                ?.length ||
                              data.question?.alternative_type ===
                                'upload_multiple'
                            ) {
                              addUploadToModel(
                                'uploads',
                                `upload${data.question?.id}`,
                                files,
                                `upload${data.question?.id}`
                              );
                            } else {
                              uploads[`upload${data.question?.id}`].files =
                                files;
                            }
                          }
                        "
                        @fileUploaded="
                          ({ files, xhr }) => {
                            const response = JSON.parse(xhr.response || '{}');
                            if (response.success) {
                              const data = response.data;

                              if (
                                data.question?.alternative_type ===
                                'website_content'
                              ) {
                                answer[
                                  data.question?.id
                                ].alternative_answer.uploads = answer[
                                  data.question?.id
                                ].alternative_answer.uploads.concat(
                                  data.map((e) => e.id)
                                );
                              } else if (
                                data.question?.alternative_type ===
                                'upload_multiple'
                              ) {
                                answer[data.question?.id].alternative_answer =
                                  answer[
                                    data.question?.id
                                  ].alternative_answer.concat(
                                    data.map((e) => e.id)
                                  );
                              } else {
                                answer[data.question?.id].alternative_answer =
                                  data[0]?.id;
                              }
                            }
                          }
                        "
                        @fileRemoved="
                          (files) => {
                            if (
                              data.question?.alternative_type ===
                              'website_content'
                            ) {
                              files
                                .map((e) => e.id)
                                .forEach((e) => {
                                  const ind = answer[
                                    data.question?.id
                                  ]?.alternative_answer.uploads?.findIndex?.(
                                    (a) => a === e
                                  );
                                  if (ind !== -1) {
                                    answer[
                                      data.question?.id
                                    ].alternative_answer.uploads.splice(ind, 1);
                                  }
                                });
                            } else if (
                              data.question?.alternative_type ===
                              'upload_multiple'
                            ) {
                              files
                                .map((e) => e.id)
                                .forEach((e) => {
                                  const ind = answer[
                                    data.question?.id
                                  ]?.alternative_answer.uploads?.findIndex?.(
                                    (a) => a === e
                                  );
                                  if (ind !== -1) {
                                    answer[
                                      data.question?.id
                                    ].alternative_answer.uploads.splice(ind, 1);
                                  }
                                });
                            } else {
                              answer[data.question?.id].alternative_answer = '';
                            }
                          }
                        "
                      ></DynamicQuestions>
                    </div>
                  </template>
                </q-card-section>
              </template>
            </q-card>
            <template v-else>
              <div v-for="j in [1, 2, 3, 4]" :key="j">
                <q-skeleton
                  :width="`${j % 2 == 0 ? 45 : 35}%`"
                  type="QToolbar"
                  class="q-my-md"
                  height="2rem"
                />
                <q-skeleton type="QToolbar" width="100%" />
              </div>
            </template>
          </template>
        </q-splitter>
      </q-card>
    </q-card>
    <q-scroll-observer @scroll="scrollHandler" />
    <image-lightbox
      :visible="showLightBox"
      :imgs="lightboxImages"
      :index="lightboxIndex"
      @hide="showLightBox = false"
    />
  </q-page>
</template>

<script>
import { ref, defineComponent, defineAsyncComponent } from 'vue';
import { mapActions, mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import { copyToClipboard, debounce, format } from 'quasar';
import { avatarNameAbbr, urlShareToEmbed } from 'src/assets/scripts/functions';
const { capitalize } = format,
  getEl = (id) => {
    return document.getElementById(id);
  };
import {
  RequestOptions_Mixin,
  FileUploader_Mixin,
  Export_Task_Project_Mixin,
  Helpers_Mixin,
} from 'src/mixins';

export default defineComponent({
  name: 'ProjectOverview',

  mixins: [
    RequestOptions_Mixin,
    FileUploader_Mixin,
    Export_Task_Project_Mixin,
    Helpers_Mixin,
  ],

  components: {
    DynamicQuestions: defineAsyncComponent(() =>
      import('src/components/DynamicQuestions.vue')
    ),
    DynamicAnswers: defineAsyncComponent(() =>
      import('src/components/DynamicAnswers.vue')
    ),
  },

  data() {
    return {
      title: 'Project Overview',
      showLightBox: false,
      lightboxIndex: 0,
      lightboxImages: [],
      tabSectionId: '',
      tabSectionIdScrolled: '',
      splitterModel: 175,
      editing: false,
      noTitleClick: false,
      documentReady: false,
      capitalize,
      avatarNameAbbr,
      urlShareToEmbed,
      scrollHandler: debounce(this.scrollHandlerOrig, 100),
      projectInfo: {},
      clickScroll: false,
      answer: ref({}),
      uploads: ref({}),
      request_submitting: false,
      request_submitted: false,
      containerSideMenu: false,
    };
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
    const response = await this.getSingleProject(this.projectId);
    if (response?.success) this.projectInfo = response.data;
    this.tabSectionId = this.hashId;
    this.updateLightBox();
    this.$nextTick(() => {
      setTimeout(() => {
        this.documentReady = true;
      }, 1000);
    });
    this.updateAnswers();
  },

  computed: {
    ...mapState(useGrowmodo, ['projectDirectory']),

    brandAvatar() {
      return this.brandInfo.avatar || {};
    },

    hashId() {
      return this.$route.hash.slice(1);
    },

    brandId() {
      return this.$route.params?.brandId;
    },

    projectId() {
      return this.$route.params?.projectId;
    },

    answers() {
      return this.projectInfo?.answers || {};
    },

    visibleAnswers() {
      const res = {};
      Object.values(this.answers).map((e) => {
        if (e.question?.show_in_overview) {
          res[e.question?.id] = e;
        }
      });
      return res;
    },

    dynamicAnswers() {
      return Object.values(this.projectInfo?.answers || {});
    },

    overviewData() {
      return this.dynamicAnswers.filter((e) => e.question?.show_in_overview);
    },

    overviewIds() {
      return this.overviewData.map((e) => `question_${e.question?.id}`);
    },

    brandInfo() {
      return this.projectInfo.brand || {};
    },

    requestInfo() {
      return this.projectDirectory[this.projectInfo?.project_id] || {};
    },

    allSections() {
      return this.overviewData.map((e, i) => ({
        label: e.question?.title,
        key: this.overviewIds[i],
      }));
    },
  },

  watch: {
    editing: {
      async handler(val) {
        setTimeout(this.updateLightBox, 500);
        this.updateAnswers();
      },
    },
    tabSectionId: {
      async handler(val) {
        if (val) {
          this.scrollToTarget(val);
          this.tabSectionId = '';
        }
      },
    },
  },

  methods: {
    ...mapActions(useGrowmodo, ['getSingleProject']),

    updateAnswers() {
      const answers = this.projectInfo?.answers || {};
      const formatedAnswers = {};
      for (const id of Object.keys(this.projectInfo?.answers || {})) {
        formatedAnswers[id] = { ...answers[id] };
        if (formatedAnswers[id]?.question) delete formatedAnswers[id]?.question;
      }
      this.answer = formatedAnswers;
    },

    updateLightBox() {
      setTimeout(() => {
        this.lightboxImages = [];
        const lightboxElems = document.querySelectorAll('.with-lightbox');
        lightboxElems.forEach((e, i) => {
          let img = e.querySelector('img');
          this.lightboxImages.push({
            src: img.src,
            title: e.getAttribute('file-name'),
            alt: e.getAttribute('file-name'),
          });
          e.onclick = () => {
            this.lightboxIndex = i;
            this.showLightBox = true;
          };
        });
      }, 500);
    },

    scrollToTarget(targetId) {
      getEl(targetId)?.scrollIntoView?.({
        block: 'start',
        inline: 'nearest',
        behavior: 'smooth',
      });
    },

    clickScrollEvent(targetId, copy = true) {
      this.clickScroll = true;
      this.tabSectionId = targetId;
      this.tabSectionIdScrolled = targetId;
      // Copy URL to Clipboard
      if (copy) {
        copyToClipboard(
          window.location.origin + this.$route.fullPath + '#' + targetId
        );
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'Anchor has been copied to clipboard.',
          timeout: 500,
        });
      }
      setTimeout(() => {
        this.clickScroll = false;
      }, 1000);
    },

    scrollHandlerOrig(scroll) {
      if (this.clickScroll) return;
      this.tabSectionIdScrolled = '';
      let selectedKey = '';
      this.allSections
        .map((e) => e.key)
        .forEach((key) => {
          const el = getEl(key),
            offsetTop = el?.getBoundingClientRect()?.top;
          const top = scroll?.position?.top;
          if (offsetTop < 50) selectedKey = key;
        });
      this.tabSectionIdScrolled = selectedKey;
    },
  },
});
</script>
